<template>
	<div class="container">
		<div :class="foodClass">
			<div class="food">{{food[0]}}</div>
			<div class="food">{{food[1]}}</div>
		</div>
		<div :class="btnClass" @click="handleClick">生成晚饭</div>
	</div>
</template>

<script>
	const FOODS = () => {
		return [
			"豆花", "东北饺子", "兰老哥米线", "水果", "呼啦原汤小面", "让菜下饭", "外卖", "冒菜", "披萨"
		]
	}
	export default {
		name: "Eat",
		data() {
			return {
				btnClass: ["button"],
				foodClass: ["food-box"],
				food: []
			}
		},
		methods: {
			handleClick() {
				let foodList = FOODS()
				let result = this.getRandomArrayElements(foodList,2)
				this.food = result
				this.btnClass = ["button", "active"]
				this.foodClass = ["food-box", "active"]
			},
			getRandomArrayElements(arr, count) {
				var shuffled = arr.slice(0),
					i = arr.length,
					min = i - count,
					temp, index;
				while (i-- > min) {
					index = Math.floor((i + 1) * Math.random());
					temp = shuffled[index];
					shuffled[index] = shuffled[i];
					shuffled[i] = temp;
				}
				return shuffled.slice(min);
			}
		}
	}
</script>

<style scoped lang="scss">
	.container {
		width: 100%;
		height: 100vh;
		position: relative;
		display: flex;
		justify-content: center;
		align-content: center;
		z-index: 10;

		.button {
			width: 100px;
			height: 100px;
			line-height: 100px;
			background-color: #f5f5f5;
			border-radius: 50%;
			box-shadow: 0 0 20px #999;
			opacity: 1;
			position: absolute;
			bottom: 50px;
			transition: all 1s ease;
			z-index: 100;
			color: #333;
		}

		.button.active {
			background-color: #fff;
			color: #000;
			box-shadow: 0 0 100px #fff;
		}

		.food-box {
			margin-top: 250px;
			transition: all 1s ease;
			transform: scale(0, 0);

			.food {
				width: 150px;
				height: 40px;
				line-height: 40px;
				background-color: #fff;
				border-radius: 14px;
				margin-top: 20px;
				box-shadow: 0 0 10px #fff;
				font-weight: bold;
			}
		}

		.food-box.active {
			transform: scale(1, 1);
		}
	}
</style>
