import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Eat from './views/Eat.vue'

Vue.use(Router)

const router = new Router({
  routes: [
		{
		  path: '/',
		  redirect: '/login'
		},
    {
      path: '/login',
      name: 'home',
      component: Home
    },
		 {
		  path: '/eat',
		  name: 'eat',
		  component: Eat
		}
  ]
})

router.beforeEach((to, from, next) => {
	if(to.path !== '/login' && !localStorage.getItem("myLover")){
		next({
			path: '/login'
		})
	}
	next()
})

export default router
